

*{
    margin: 0;
    padding:0;
    box-sizing: border-box;
  }
  
  html {
    scroll-behavior: smooth;
  }
  
  body{
    overflow-x: hidden;
  }
  
  @font-face {
    font-family: BebasNeue;
    src: url(./components/assets/fonts/Bebas/BebasNeue-Regular.ttf);
  }
  
